/******************/
/* ZEBRA SETTINGS */
/******************/



/***********************/
/* RESPONSIVE SETTINGS */
/***********************/

  // Enable/Disable the output of classes within media queries
  $zeb-enable-responsive: true !default;

  // Enable/Disable mobile first media queries
  $zeb-mobile-first: true !default;

  // Media query breakpoints
  $zeb-media-widths: 435, 500, 599, 768, 1024 !default;
  // TODO: add option to set unit type

  // Aliases for the media query breakpoints
  $zeb-media-width-names: xs, s, m, l, xl !default;

/* RESPONSIVE SETTINGS END */







/* BROWSER RESET */
$zeb-enable-browser-reset: true !default;
$zeb-box-sizing: true !default;


$zeb-site-width: 1400px !default;

/* RESET FONT SIZE */
$zeb-enable-reset-font: true !default;
$zeb-reset-font-size: 16px !default;

/* COLUMN FRACTIONS */
$zeb-columns: 5, 8, 12 !default;

/* POSITIONING */
$zeb-enable-offset: false !default;
$zeb-enable-push: false !default;
$zeb-enable-pull: false !default;

/* SPACING */
$zeb-enable-padding: true !default;
$zeb-enable-margin: true !default;
$zeb-spacings: 10, 25 !default;

/* GRIDS */
$zeb-enable-grids: true !default;
$zeb-safe-grid-classes: false !default;
$zeb-grid-columns: 2, 3, 4 !default;

/* GUTTERS */
$zeb-enable-gutters: true !default;
$zeb-gutter-sizes: 10, 25 !default;



/*  NEED ORGANISING*/
$zeb-format--col: "-" !default;

$zeb-enable-hidden: true !default;